.season {
  width: 10px;
  margin: 10px;
}

.season-name {
  display: flex;
  justify-content: center;
  font-size: 12px;
}

.season-block {
  display: flex;
  flex-direction: column-reverse;
  width: 10px;
  height: 50px;
  margin: 0 auto;
}

.season-block_number {
  align-self: flex-end;
  width: 10px;
  height: 50px;
  background-color: #529734;
}
